  figure span {
    display: block;
    width: 50px;
    height: 50px;
    background-position: center;
  }
  .header-image {
    border-radius: 15px;
    overflow: hidden;
    min-height: 420px;
    background-image: url(https://cdn.bama.ir/uploads/BamaImages/Corporations/corporationbanner_637635870717859060.jpg) !important;
    background-size: 100% 100% !important;
  }

  .brand-logo {
    width: 150px;
    height: 150px;
    border-radius: 100px;
    border: 5px solid #e2e2e2;
  }

  .brand-wrapper {
    margin-right: 5rem;
  }

  .search-box-complete {
    width: 450px;
    background-color: #fff;
    margin-top: 15px;
    border-radius: 30px;

    font-size: 15px;

    border: none;
  }

  .imgIcon {
    width: 88px !important;
    height: 88px !important;
  }

  .loadingList {
    position: fixed;
    bottom: 40px;
    background-color: #fff;
    border-radius: 50px;
    display: flex;
    right: 53%;
    transition: all 0.3s ease-in;
    opacity: 0;
    visibility: hidden;
    transform: translateY(50px);
  }

  .loadingList.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
